import React from 'react'
import ModelStore from "../../stores/ModelStore";
import * as MyActions from "../../actions/MyActions";
import { dict } from '../../Dict';
import { conf } from '../../conf';
import queryString from 'query-string'

var t = dict['farsi']

export default class LoginJwt extends React.Component {

    constructor(props) {
        super(props);
        this.getList = this.getList.bind(this);
        this.handleChange = this.handleChange.bind(this);

        this.state = {
            lang: window.localStorage.getItem('lang'),
            dir: window.localStorage.getItem('dir'),
            token: null,
            notFound: false,
        }

    }



    componentWillMount() {
        ModelStore.on("got_list", this.getList);
    }

    componentWillUnmount() {
        t = dict[this.state.lang]
        ModelStore.removeListener("got_list", this.getList);
    }

    componentDidMount() {
        document.dir = this.state.dir
        MyActions.getList('events/shortname_list', 1, { shortname: this.props.match.params.shortname }, this.state.token);
        // MyActions.getList('events/shortname_list', {}, this.props.match.params.id);
    }



    handleChange(obj) {
        this.setState(obj);
    }

    getList() {
        var klass = ModelStore.getKlass()
        var list = ModelStore.getList()
        if (klass === 'Event' && list && list[0]) {
            window.localStorage.setItem('event_id', list[0].id);
            window.localStorage.setItem('event_name', list[0].shortname);
            this.props.history.push("/events")
        } else {
            this.setState({ notFound: true })
        }
        console.log(list)
    }

    languageBtn() {
        return (

            <div class="dropdown">
                <button type="button" onClick={() => this.setState({ langBtnActive: !this.state.langBtnActive })} className={this.state.langBtnActive ? "btn btn-secondary dropdown-toggle show" : "btn btn-secondary dropdown-toggle"} data-bs-toggle="dropdown">
                    <span className='mx-1'>Language</span>
                    <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none" /><path d="M5 7h7m-2 -2v2a5 8 0 0 1 -5 8m1 -4a7 4 0 0 0 6.7 4" /><path d="M11 19l4 -9l4 9m-.9 -2h-6.2" /></svg>
                </button>
                <div className={this.state.langBtnActive ? "dropdown-menu show" : "dropdown-menu"}>
                    <a class="dropdown-item" onClick={() => this.changeLang('farsi', 'rtl')}>
                        فارسی
                    </a>
                    <a class="dropdown-item" onClick={() => this.changeLang('english', 'ltr')}>
                        English
                    </a>
                </div>
            </div>


        )
    }

    changeLang(lang, dir) {
        window.localStorage.setItem('lang', lang)
        window.localStorage.setItem('dir', dir)
        window.location.reload()
    }

    notFoundNotice() {
        if (this.state.notFound) {
            <div class="empty">
                {this.languageBtn()}
                <div class="empty-img">
                    <div className="demo-icon-preview"><svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none"></path><path d="M12 9v2m0 4v.01"></path><path d="M5 19h14a2 2 0 0 0 1.84 -2.75l-7.1 -12.25a2 2 0 0 0 -3.5 0l-7.1 12.25a2 2 0 0 0 1.75 2.75"></path></svg></div>
                </div>
                <p class="empty-title">{t['event_not_found_by_name']}</p>
                <p class="empty-subtitle text-muted">
                    {t['not_found_note']}
                </p>
                <div class="empty-action">
                    <a href="/" class="btn btn-primary">
                        <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none"></path><line x1="5" y1="12" x2="19" y2="12"></line><line x1="5" y1="12" x2="11" y2="18"></line><line x1="5" y1="12" x2="11" y2="6"></line></svg>
                        {t['take_me_home']}
                    </a>
                </div>
            </div>
        }
    }

    render() {
        return (
            <div class="flex-fill d-flex align-items-center justify-content-center">
                <div class="container-tight py-6">
                    {this.notFoundNotice()}
                </div>
            </div>
        )
    }
}




